import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
import { Storage } from '@ionic/storage';
import { Events,Platform } from 'ionic-angular';
import { Network } from '@ionic-native/network';

/*
  Generated class for the ExerciseProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class OfflineProvider {
    
    public properties:any;
    public heartbeat:any;
    public completedRequests:Array<any>;
    
    constructor(public http: HttpClient, public storage: Storage, public events: Events, private platform: Platform, private network: Network) {
        
        this.properties = {inProgress:false, heartbeatStarted:false, requestCount:0};
        this.completedRequests = [];
        
        this.network.onConnect().subscribe(data => {
            this.events.publish("app:online");
            setTimeout(() => {this.doRequests();},2000);
        }, error => {
            //console.error(error)
        });

        this.network.onDisconnect().subscribe(data => {
            this.events.publish("app:offline");
            //this.startHeartbeat();
        }, error => {
            //console.error(error)
        });  
        
        this.platform.ready().then(() => {
            if ((!this.network.type || this.network.type === "none") && this.platform.is("cordova")){
                this.events.publish("app:offline");
            }

            this.storage.get("failedRequests").then((requests) => {
                if (requests){
                    if (requests.length > 0 && this.network.type && this.network.type !== "none"){
                        this.doRequests();

                    }
                }
            })
        });
          
        
        
        this.events.subscribe("app:heartbeat", () => {
            this.startHeartbeat();
        })
        
    }


    public doRequests(){
        if (this.properties.inProgress){ 
            return;
        }


        this.properties.inProgress = true;
        
        this.storage.get("session").then((session) => { 
            if (session){ 
                this.storage.get("failedRequests").then((requests) => {
                    if (requests && requests.length > 0){
                        this.properties.requestCount = requests.length;
                        
                        
                        this.completedRequests = [];
                        
                        for (var index in requests){

                            let request = requests[index];

                            this.http.post(AppSettings.apiUrl, request).subscribe((res) => {
                                this.updateRequestCount();

                                if (res["success"] === true){

                                    
                                    //remove requestid from item and add proper id
                                    
                                    let id = res["data"]["id"];
                                    let requestCopy = Object.assign({},request);
                                    this.updateStorage(id,requestCopy);
                                    
                                    //remove from storage
                                    this.completedRequests.push(index);
                                    
   
                                }
                                else {
                                    this.completedRequests.push(index); //wasnt an error from the internet, so remove it
                                }                      
                            }, () => {
                                this.updateRequestCount();
                            })  


                            

                        } 
                    }
                    else{
                        this.properties.inProgress = false;
                    }      


                }) 
            }
            else{
                this.properties.inProgress = false;
            }
        });     
    }
    
    
    private updateRequestCount(){
        this.properties.requestCount -= 1;

        if (this.properties.requestCount < 1){
            this.properties.inProgress = false;
            

            
            this.events.publish("requests:completed");
            
            
            this.storage.get("failedRequests").then((requests) => {
                if (requests){
                    this.completedRequests.sort(function(a, b){return parseInt(b)-parseInt(a)});
                    for (let requestIndex of this.completedRequests){
                        requests.splice(requestIndex, 1);
                    }  
                    this.storage.set("failedRequests", requests);             
                } 
            })
            
            
        }        
    }
    
    public startHeartbeat(){
        if (this.properties.heartbeatStarted){return;}
        
        this.events.publish("app:offline");
        this.properties.heartbeatStarted = true;
        this.heartbeat = setInterval(() => {
            this.http.post(AppSettings.apiUrl, {key: AppSettings.apiKey}).subscribe((res) => {
                this.events.publish("app:online");
                clearInterval(this.heartbeat);
                this.properties.heartbeatStarted = false;
                this.doRequests();
            });
            
        },20000); 
    }
    
    private updateStorage(id,request){
        if (request.action === "addresults"){
            this.storage.get("workouts").then((workouts) => { 
                 if (workouts && workouts[request.assigneddate]){
                    for (var index in workouts[request.assigneddate]){
                        let workoutExercise = workouts[request.assigneddate][index];
                        if (workoutExercise.exerciseid === request.exerciseid){
                            for (var setIndex in workoutExercise.sets){
                                if (workoutExercise.sets[setIndex].requestId && workoutExercise.sets[setIndex].requestId === request.requestId){
                                    workouts[request.assigneddate][index].sets[setIndex].requestId = null;
                                    workouts[request.assigneddate][index].sets[setIndex].id = id;
                                }
                            }
                        }  
                    } 
                    
                    this.storage.set("workouts", workouts);
                                      
                 }
            });    
        }
        else if (request.action === "savebodyweight"){
            this.storage.get("bodyweights").then((bodyweights) => { 
                if (bodyweights){
                    for (var index in bodyweights){
                        let bodyweight = bodyweights[index];
                        if (bodyweight.requestId === request.requestId){
                            bodyweights[index].requestId = null;
                            bodyweights[index].id = id;
                        }
                    }  
                    this.storage.set("bodyweights", bodyweights);
                }
              
            });
            
        }
        
        
    }


}



