import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';

import { ExerciseProvider } from '../../providers/exercise/exercise';
import { Storage } from '@ionic/storage';

import {ExerciseSearchPipe} from '../../pipes/exercise-search';
import * as moment from 'moment';

import {SelectExerciseModal} from '../../modals/select-exercise/select-exercise';

import { AccountProvider } from '../../providers/account/account';

@Component({
    selector: 'goal-resets',
    templateUrl: 'goal-resets.html'
})

export class GoalResetsModal {
    public resets:any;
    public account:any;
    
    constructor(public platform: Platform, public params: NavParams, public viewCtrl: ViewController, private toastCtrl: ToastController, private exerciseProvider: ExerciseProvider, public storage: Storage, private alertCtrl: AlertController, public modalCtrl: ModalController, private accountProvider: AccountProvider, public events: Events) {
        this.resets = [];
        this.account = {};
        
        this.storage.get("account").then((data) => {
            this.account = data;          
        });  
        
        this.accountProvider.getResets().then((data) => {
            this.resets = data;
        });       
        
    }

    
    public openSelectExercise(reset){
        let modal = this.modalCtrl.create(SelectExerciseModal); 
        modal.onDidDismiss(exercise => {
            if (exercise){
                reset.exerciseid = exercise.id;
                reset.name = exercise.name;
                this.updateReset(reset);
                
            }
        })
        modal.present();         
    }
    
    public createReset(){
        let reset = {id:null, exerciseid:"",name:"",resetdate:""};
        this.resets.push(reset);
        this.events.publish("goals:updated",this.account);
        //create reset
        this.accountProvider.addReset(this.account.id).then((data:any) => {
            reset.id = data.id;
        });
        
    }
    
    public deleteReset(index, reset){
        this.resets.splice(index,1);
        this.events.publish("goals:updated",this.account);
        //delete reset
        this.accountProvider.removeReset(reset,this.account.id);
        
    }
    
    public updateReset(reset){
        //update reset
        this.events.publish("goals:updated",this.account);
        this.accountProvider.updateReset(reset,this.account.id);
    }
       

    public dismiss() {
        this.viewCtrl.dismiss();
    }
}






