import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { EmailComposer } from '@ionic-native/email-composer';
import { InAppBrowser } from '@ionic-native/in-app-browser';

@Component({
    selector: 'help',
    templateUrl: 'help.html'
})

export class HelpModal {
    public contact:any;
    public account:any;
    
    constructor(public viewCtrl: ViewController, public storage: Storage, private emailComposer: EmailComposer, private iab: InAppBrowser) {
        this.contact = {subject:"",message:""};
        
        this.account = {};
        
        this.storage.get("account").then((data) => {
            this.account = data;
        });         
    }

    public sendMessage(){
        let email = {
          to: ["support@intensityapp.com"],
          subject: "Suport request: " + this.contact.subject,
          body: 'UserID: ' + this.account.id + '<br>User Email: ' + this.account.email + '<br>Message: ' + this.contact.message,
          isHtml: true
        };        

        this.emailComposer.open(email);        
    }
    
    public openLink(link){
        window.open(link, '_system');
    }
    
    dismiss() {
        this.viewCtrl.dismiss();
    }
}


