import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, Events, ModalController, LoadingController, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Storage } from '@ionic/storage';
import { OneSignal } from '@ionic-native/onesignal';

import { DiaryPage } from '../pages/diary/diary';
import { FriendsPage } from '../pages/friends/friends';
import { MessagesPage } from '../pages/messages/messages';
import { LeaderboardPage } from '../pages/leaderboard/leaderboard';
import { PremiumPage } from '../pages/premium/premium';
import { ProfilePage } from '../pages/profile/profile';
import { ProgramsPage } from '../pages/programs/programs';
import { RecordsPage } from '../pages/records/records';
import { StatsPage } from '../pages/stats/stats';
import { SettingsPage } from '../pages/settings/settings';

import {LoginModal} from '../modals/login/login';
import {AppSettings} from './app.settings';
import * as moment from 'moment';
import { AccountProvider } from '../providers/account/account';
import { OfflineProvider } from '../providers/offline/offline';

import { AuthenticationProvider } from '../providers/authentication/authentication';

import { InAppPurchase } from '@ionic-native/in-app-purchase';

import { LocalNotifications } from '@ionic-native/local-notifications';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
    @ViewChild(Nav) nav: Nav;

    rootPage: any = DiaryPage;
    
    premiumPage: any;
    profilePage: any;

    pages: Array<{title: string, component: any, icon: string}>;

    account: any;
    
    theme:string;
    
    connectionStatus:boolean;

    constructor(public platform: Platform, public statusBar: StatusBar, public modalCtrl: ModalController, public splashScreen: SplashScreen, private accountProvider: AccountProvider, public events: Events, public storage: Storage, public loadingCtrl: LoadingController,private auth: AuthenticationProvider, private alertCtrl: AlertController, private oneSignal: OneSignal, private offlineProvider: OfflineProvider, private iap: InAppPurchase, private localNotifications: LocalNotifications) {
        this.initializeApp();

        // used for an example of ngFor and navigation
        this.pages = [
            { title: 'Diary', component: DiaryPage, icon:"bookmarks" },
            { title: 'Records', component: RecordsPage, icon:"trophy" },
            { title: 'Stats', component: StatsPage, icon:"stats" },
            { title: 'Leaderboard', component: LeaderboardPage, icon:"ribbon" },
            { title: 'Friends', component: FriendsPage, icon:"people" },
            { title: 'Messages', component: MessagesPage, icon:"chatbubbles" },
            { title: 'Programs', component: ProgramsPage, icon:"calendar" },
            { title: 'Settings', component: SettingsPage, icon:"settings" }
        ];
        
        this.account = {username:"John Doe", dp: "http://api.intensityapp.com/uploads/default.png"};

        this.storage.get('session').then((session) => {
            if (!session){
                this.openLogin();
            }
            else{
                this.getAccount();
                this.savePushId();  
            }


        }); 
        
        this.connectionStatus = true;
        
        this.events.subscribe('app:online', () => {
            this.connectionStatus = true;
        });
        this.events.subscribe('app:offline', () => {
            this.connectionStatus = false;
        });        
        
        
        this.premiumPage = {title: 'Premium', component: PremiumPage};
        this.profilePage = {title: 'Profile', component: ProfilePage};
        
        
        this.events.subscribe('workout:added', (workoutDateObj) => {
            let workoutDate = workoutDateObj.date;
            if (moment(workoutDate).isSame(moment(), 'day') && (moment(workoutDate).isAfter(this.account.last_workout, 'day') || moment(workoutDate).isSame(this.account.last_workout, 'day'))){
                this.account.last_workout = workoutDate;
                this.account.last_workout_formatted = "today";
            }
            else if (this.account.last_workout_formatted === "never" || moment(workoutDate).isAfter(this.account.last_workout, 'day')){
                this.account.last_workout = workoutDate;
                this.account.last_workout_formatted = moment(this.account.last_workout).fromNow();
            }
        });
        
        this.events.subscribe('profile:updated', (profile) => {
            this.account.dp = profile.dp;
            this.account.username = profile.username;
        });        
        
        this.events.subscribe('user:logout', () => {
            this.nav.setRoot(DiaryPage);
            this.nav.popToRoot();
            this.openLogin();
        });
        
        this.storage.get("theme").then((data) => {
            if (data){
                this.theme = data;
                if (this.theme === "dark"){
                    document.querySelector("ion-app").className += " dark-theme";
                }
            }
        });
                
        this.events.subscribe('theme:updated', (theme) => {
            this.theme = theme;
            if (this.theme === "dark"){
                document.querySelector("ion-app").className += " dark-theme";
            }
            else{
                if (document.querySelector("ion-app").classList.contains("dark-theme")){
                    document.querySelector("ion-app").classList.remove("dark-theme");

                }
            }
        });
        
        this.events.subscribe("premium:purchased", () => {
            this.account.premium = true;
        });                        

    }
  
    openLogin(){
        let modal = this.modalCtrl.create(LoginModal); 
        modal.onDidDismiss(data => {
            if (data){
                this.events.publish('session:retreived');
                this.getAccount();
                this.savePushId();  
            }
            else{
                
                //skipped, create an anonymous user account
                
                let loading = this.loadingCtrl.create({
                    content: 'Creating anonymous user...'
                });  
                loading.present();  
                
                this.auth.registerAnonymous().then((account) => {
                    this.events.publish('session:retreived');
                    this.getAccount();    
                    this.savePushId();                
                    loading.dismiss();
                    
                    let alert = this.alertCtrl.create({
                        title: 'Success',
                        subTitle: 'Your anonmyous user account has been created',
                        message: 'Login on other platforms using <strong>' + account["username"] + '</strong> with password <strong>' + account["password"] + '</strong>. Change this in your settings.',
                        buttons: [
                            {
                                text: 'OK',
                                role: 'cancel',
                                handler: data => {
                                }
                            }
                        ]
                    }); 
                    
                    setTimeout(() => {alert.present()},200);  
                                       
                }).catch(() => {
                    loading.dismiss();
                    let alert = this.alertCtrl.create({
                        title: 'Error',
                        message: 'There was an issue creating your anonymous user account.',
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel',
                                handler: data => {
                                    this.openLogin();
                                }
                            }
                        ]
                    });
                    setTimeout(() => {alert.present()},200);                      
                    
                })
                               
                
            }
        })
        modal.present();      
    }
  
    getAccount(){
        this.accountProvider.getAccount().then((account) => {
            this.account = account;
            if (this.account.last_workout){
                this.account.last_workout_formatted = moment(this.account.last_workout).fromNow();
            }
            else{
                this.account.last_workout = new Date();
                this.account.last_workout_formatted = "never";
            }

            this.checkPremium();

            this.events.publish('user:retreived');
            
            
            

        });      
    }
    
    checkPremium(){

        if (this.platform.is('cordova')){
            let premiumDate = moment(this.account.premium_date).add(1, 'months');
            if (this.account.premium && moment(this.account.premium_date).add(1, 'months').isBefore(moment())){
                //check purchases
                this.iap.restorePurchases().then((purchases) => {
                    let renewed = false;
                    let productId = this.platform.is("android") ? "com.taylorhamling.intensity.premium2" : "com.taylorhamling.intensity.premium";
                    for (let purchase of purchases){
                        if (!purchase.date && purchase.receipt){
                            let receipt = JSON.parse(purchase.receipt);
                            purchase.date = receipt.purchaseTime;
                        }                        
                        if (moment(purchase.date).add(1, 'months').isAfter(moment()) && purchase.productId === productId){
                            premiumDate = moment(purchase.date);
                            renewed = true;
                            break;
                        }
                    }

                    if (renewed){
                        this.account.premium_date = premiumDate.format("YYYY-MM-DD");
                        this.accountProvider.updateSettings({premiumdate:this.account.premium_date}, this.account.id);
                    }
                    else{
                        this.account.premium = false;
                        this.accountProvider.updateSettings({premium:this.account.premium}, this.account.id);
                    }

                })
            }
        }        
    }
    
    savePushId(){
        this.oneSignal.getIds().then((data) => {
            if (data.userId){
                //save
                this.auth.savePushId(data.userId);
            }
        }).catch((e) => {
            //console.log(e);
        });        
    }
    

    initializeApp() {
        this.platform.ready().then(() => {
            // Okay, so the platform is ready and our plugins are available.
            // Here you can do any higher level native things you might need.
           
            
            if (this.platform.is("android")) {
                this.statusBar.backgroundColorByHexString("#9c2111");
                this.statusBar.styleLightContent();
            }
            else{
                this.statusBar.styleLightContent();
            }
            
            
            this.splashScreen.hide();
            
            this.localNotifications.clearAll();
            
            if (this.platform.is('cordova')){
                this.oneSignal.startInit("f500d613-213a-4a7b-9be0-a101ecbc36ed", "654916760436");

                this.oneSignal.handleNotificationOpened().subscribe((data) => {
                    if (!data.notification.isAppInFocus){
                        let pushData = data.notification.payload.additionalData;
                        if (pushData.type === "friends"){
                            setTimeout(() => {this.nav.push(FriendsPage);},500);
                        }
                        else if (pushData.type === "messages"){
                            setTimeout(() => {this.nav.push(MessagesPage);},500);
                        }
                        else if (pushData.type === "leaderboard"){
                            setTimeout(() => {this.nav.push(LeaderboardPage);},500);
                        }
                    }
                });

                this.oneSignal.endInit();
            }
            
        });
    }

    openPage(page) {
        // Reset the content nav to have just this page
        // we wouldn't want the back button to show in this scenario
        this.nav.setRoot(page.component);
    }
}



