import { Injectable } from '@angular/core';
import { TimerObservable } from 'rxjs/observable/TimerObservable';
import { Observable } from 'rxjs/Rx';
import { NativeAudio } from '@ionic-native/native-audio';
import { Events,Platform } from 'ionic-angular';

import { LocalNotifications } from '@ionic-native/local-notifications';

import { BackgroundMode } from '@ionic-native/background-mode';

@Injectable()    
export class TimerService {
    
    private timer: any;
    
    public stopwatch: number;
    public stopwatchProperties: any;
    
    public countdownTimer: number;
    public countdownTimerProperties: any;
    
    private stopwatchSubscription: any;
    private timerSubscription: any;
    
    private pauseTimestamp: number;

    constructor(private nativeAudio: NativeAudio, public events: Events, private platform: Platform, private localNotifications: LocalNotifications, private backgroundMode: BackgroundMode) {
        this.stopwatch = 0;
        this.countdownTimer = 60000;
        this.countdownTimerProperties = {playSound:false,repeat:false, time:60000, started:false, force:false};
        this.stopwatchProperties = {started:false};
        this.timer = TimerObservable.create(0, 10);
        this.nativeAudio.preloadComplex('timerFinished', 'assets/audio/timer.mp3', 1, 1, 0);
        this.backgroundMode.setDefaults({silent:true});
        
        platform.ready().then(() => {    
            this.platform.pause.subscribe(() => {
                if (this.stopwatchProperties.started || this.countdownTimerProperties.started){
                    this.localNotifications.schedule({
                      id: 1,
                      title: 'Timer is running',
                      text: 'Continue your session!',
                      ongoing:true
                    });
                    this.pauseTimestamp = Math.floor(Date.now());
                    
                    if (this.countdownTimerProperties.started && this.countdownTimerProperties.force){
                        if (this.countdownTimerProperties.repeat){
                            for (let x = 0; x < 10; x++){
                                this.localNotifications.schedule({
                                  id: 2 + x,
                                  title: 'Timer has completed (will repeat)',
                                  text: 'Ran for ' + (this.countdownTimerProperties.time / 1000) + " seconds",
                                  at: new Date(new Date().getTime() + (this.countdownTimer + (this.countdownTimerProperties.time*x)))
                                });                                
                            }                            
                        }
                        else{
                            this.localNotifications.schedule({
                              id: 2,
                              title: 'Timer has completed',
                              text: 'Ran for ' + (this.countdownTimerProperties.time / 1000) + " seconds",
                              at: new Date(new Date().getTime() + this.countdownTimer)
                            });                            
                        }
                    }
                }
     
            });
            

            this.platform.resume.subscribe(() => {
                this.localNotifications.clearAll();
                

                if (this.stopwatchProperties.started){
                    this.stopwatch += (Math.floor(Date.now()) - this.pauseTimestamp);
                }
                
                if (this.countdownTimerProperties.started){
                    let newTime = this.countdownTimer - (Math.floor(Date.now()) - this.pauseTimestamp);
                    
                    if (newTime < 0 && this.countdownTimerProperties.repeat){
                        this.stopTimer();
                        let remainder = Math.abs(newTime) % this.countdownTimerProperties.time;
                        
                        this.setTimer(remainder);
                        this.startTimer();
                    }
                    else if (newTime < 0){
                        this.stopTimer();
                        this.setTimer(0);
                    }
                    else{
                        this.stopTimer();
                        this.setTimer(newTime);
                        this.startTimer();
                    }
                }                
                
            });
        });        
        
        
    }

    
    startStopwatch(){
        this.stopwatchProperties.started = true;
        
        
        this.stopwatchSubscription = this.timer.subscribe(t => { 
            this.stopwatch += 10; 
        });
        
        
        this.events.publish("stopwatch:started");       
         
    }
    
    stopStopwatch(){
        this.stopwatchProperties.started = false;
        
        this.stopwatchSubscription.unsubscribe();
        
        this.events.publish("stopwatch:stopped");
    }
    
    resetStopwatch(){
        this.stopwatch = 0;
    }
    
    
    startTimer(){
        this.countdownTimerProperties.started = true;
        this.backgroundMode.enable();
        
        if (this.countdownTimer <= 0){
            this.resetTimer();
        }
        
        /*
        this.timerSubscription = setInterval(() => {
            this.countdownTimer -= 10;
            if (this.countdownTimer <= 0){
                if (this.countdownTimerProperties.playSound){
                    this.nativeAudio.play('timerFinished');
                }
                if (this.countdownTimerProperties.repeat){
                    this.resetTimer();
                }
                else{
                    this.stopTimer();
                }
            }             
        },10)
        
        */
        
        
        this.timerSubscription = this.timer.subscribe(t => { 
            this.countdownTimer -= 10;
            if (this.countdownTimer <= 0){
                if (this.countdownTimerProperties.playSound){
                    this.nativeAudio.play('timerFinished');
                }
                if (this.countdownTimerProperties.repeat){
                    this.resetTimer();
                }
                else{
                    this.stopTimer();
                }
            } 
        });  
        
        
        
               
        
        this.events.publish("timer:started");       
    }
    
    stopTimer(){
        this.countdownTimerProperties.started = false;
        this.backgroundMode.disable();
        
        //clearInterval(this.timerSubscription);
        
        this.timerSubscription.unsubscribe();
        
        this.events.publish("timer:stopped");
    }
    
    resetTimer(){
        this.countdownTimer = this.countdownTimerProperties.time;
    }    
    
    setTimer(seconds:number){
        this.countdownTimer = seconds;
    }
    
    updateTimerOptions(options:any){
        Object.assign(this.countdownTimerProperties, options);
        this.resetTimer();
    }
}


