import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events } from 'ionic-angular';


import { Storage } from '@ionic/storage';

import { InAppBrowser } from '@ionic-native/in-app-browser';

import { Camera, CameraOptions } from '@ionic-native/camera';

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

import {AppSettings} from '../../app/app.settings';

import { PremiumPage } from '../../pages/premium/premium';

@Component({
    selector: 'edit-set',
    templateUrl: 'edit-set.html'
})

export class EditSetModal {

    public set: any;
    public exercise: any;
    public properties: any;
    public session: string;
    public account:any;
    
    constructor(public navCtrl: NavController, public platform: Platform, public params: NavParams, public events: Events, public viewCtrl: ViewController, private toastCtrl: ToastController,public storage: Storage, private alertCtrl: AlertController, private iab: InAppBrowser, private camera: Camera,private transfer: FileTransfer, private file: File) {
        this.properties = {};
        
        
        this.set = {};
        Object.assign(this.set, this.params.data.set)
        this.set.rpeScaled = parseFloat(this.set.rpe) * 10;
        this.exercise = this.params.data.exercise;
        this.set.updateAll = false;
        
        
        this.storage.get("session").then((session) => { 
            this.session = session;
        });
        
        this.storage.get("account").then((data) => {
            this.account = data;
        });           
        
        
        this.events.subscribe("premium:purchased", () => {
            this.account.premium = true;
        });                  
        
    }
    
    
    
    public changeRpe(amount){
        this.set.rpeScaled = this.set.rpeScaled + amount;
        if (this.set.rpeScaled < 60){
            this.set.rpeScaled = 60;
        }
        else if (this.set.rpeScaled > 100){
            this.set.rpeScaled = 100;
        }
    }
    
    
    private determinePercentage(){
        let percentages = {0:0,1:100,2:95,3:90,4:88,5:86,6:83,7:80,8:78,9:76,10:75,11:72,12:70,13:66,14:63,15:60};
        let repRounded = Math.floor(this.set.reps);
        this.set.percentage =  repRounded > 15 ? 50 : percentages[repRounded];;
    }  
    
    public viewVideo(){
        window.open(this.set.video, '_system');
    }  
    
    public uploadVideo(){

        if (!this.account.premium){
            this.navCtrl.push(PremiumPage);
            return;
        }        
        
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,
            mediaType: this.camera.MediaType.VIDEO
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {
                
                this.properties.uploadingVideo = true;
                
                const fileTransfer: FileTransferObject = this.transfer.create();                

                let options: FileUploadOptions = {
                    fileKey:"fileToUpload",
                    fileName: this.exercise.name + this.set.id,
                    params: {key: AppSettings.apiKey, session: this.session, controller:"edit", action:"uploadvideo", id:this.set.id},
                    mimeType : "video/mp4"
                }

                fileTransfer.upload(image, encodeURI(AppSettings.apiUrl), options).then((data) => {
                    var response = JSON.parse(data.response);
                    if (response.success === true){
                        this.set.video = AppSettings.apiUrl.replace("index.php", "") + response.data;
                    } 
                    else{
                        let alert = this.alertCtrl.create({
                            title: "Error",
                            subTitle: "There was a problem uploading your video",
                            message: JSON.stringify(response),
                            buttons: [
                                {
                                    text: 'Dismiss',
                                    role: 'cancel'
                                }
                            ]
                        });
                        alert.present();                         
                    }                      
                    this.properties.uploadingVideo = false;
                }, (err) => {
                    let alert = this.alertCtrl.create({
                        title: "Error",
                        subTitle: "There was a problem uploading your video",
                        message: JSON.stringify(err),
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();   
                    this.properties.uploadingVideo = false;                 
                })


            }, 
            err => {
                this.properties.uploadingVideo = false;
                //console.log(err)
            });         

    }
    
    public saveSet(){
        this.set.rpe = this.set.rpeScaled / 10;
        this.viewCtrl.dismiss(this.set);
    }
    
    public openDelete(){
        let alert = this.alertCtrl.create({
            title:"Delete Set",
            message: "Are you sure you want to delete this set?",
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel'
                },
                {
                    text: 'Yes',
                    handler: data => {
                       this.viewCtrl.dismiss({deleted:true});
                   }
                }
            ]
        });
        alert.present();         
    }

    public dismiss() {
        this.viewCtrl.dismiss();
    }
}






